
.switch {
	position: relative;
	display: inline-block;
	width: 50px;
	height: 24px;
	margin-right: 10px;
	vertical-align: middle;
}

.switch input {
	opacity: 0;
	width: 0;
	height: 0;
}

.slider {
	position: absolute;
	cursor: pointer;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #ccc;
	transition: 0.4s;
	border-radius: 24px;
}

.slider:before {
	position: absolute;
	content: "";
	height: 18px;
	width: 18px;
	left: 3px;
	bottom: 3px;
	background-color: white;
	transition: 0.4s;
	border-radius: 50%;
}

.switch input:checked + .slider {
	background-color: #2271b1;
}

.switch input:checked + .slider:before {
	transform: translateX(26px);
}

.switch-label {
	vertical-align: middle;
	font-weight: 500;
}

.cpm-toggle-list {
	display: flex;
	flex-direction: column;
	gap: 10px;
}

.cpm-toggle-item {
	display: flex;
	align-items: center;
	gap: 8px;
}
.cpm-disabled {
	opacity: 0.5;
	pointer-events: none;
}
